@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap");

@import url("https://fonts.googleapis.com/css2?family=Libre+Baskerville:wght@400;700&display=swap");

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: "Poppins", sans-serif;
    height: calc(100vh - 5rem);
    background-image: url("./assets/images/todo-bg.jpg");
    background-repeat: no-repeat;
    background-size: cover;
}

.header h2 {
    text-align: center;
    font-family: "Libre+Baskerville";
    font-size: 2rem;
    font-weight: 700;
    margin: 5rem auto 5px;
    padding: 10px;
}

.container {
    width: 500px;
    margin: 30px auto;
    background-color: #f0f8ff;
    border: none;
    border-radius: 5px;
    box-shadow: 5px 10px 20px #4d4d4d;
    overflow: auto;
}

.add-todo {
    display: flex;
    justify-content: center;
    padding: 30px;
    margin-top: 20px;
}

.input-todo {
    width: 50%;
    padding: 10px;
    border: none;
    border-radius: 0;
    border-bottom: 2px solid #9f9f9f;
}

.input-todo:focus {
    outline: none;
    border-bottom: 2px solid #0099ff;
}

.add-btn {
    background-color: #f0f8ff;
    font-size: 16px;
    font-weight: 600;
    margin: 2px 10px;
    padding: 6px;
    border: none;
    border-radius: 50%;
}

.add-btn:hover {
    transform: scaleX(1.1) scaleY(1.1);
    background-color: #fff;
}

.remove-btn {
    display: block;
    background-color: #0099ff;
    color: #fff;
    font-size: 14px;
    font-weight: 400;
    margin: 20px auto;
    padding: 8px 13px;
    border: none;
    border-radius: 5px;
    text-transform: uppercase;
}

.remove-btn:hover {
    transform: scaleX(1.06) scaleY(1.06);
    opacity: 0.8;
}

.show-todo .space {
    height: 20px;
}

.each-todo {
    display: flex;
    justify-content: space-between;
    width: 90%;
    margin: auto;
    padding: 8px;
    border-radius: 4px;
    background-color: #fff;
    box-shadow: 2px 5px 10px #4d4d4d;
}

.each-todo h3 {
    font-size: 15px;
    padding-top: 2px;
}

.each-todo .wrapper button {
    margin-right: 10px;
    padding: 2px;
}

.each-todo .wrapper button:hover {
    background-color: #9f9f9f;
    transform: scale(1.1);
}

@media screen and (max-width: 560px) {
    .container {
        width: 80%;
    }
    .input-todo {
        width: 70%;
    }
}
